<template>
  <div id="orderRecord">
    <van-pull-refresh
      v-model="pullLoading"
      :success-text="successMessage"
      @refresh="pullRefresh"
      :head-height="100"
      :disabled="disablePullRefresh"
    >
      <van-list
        v-model="pushLoading"
        :immediate-check="false"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        :error.sync="pushError"
        error-text="请求失败，点击重新加载"
      >
        <div class="orderList">
          <div class="order" v-for="item in 9" :key="item">
            <div class="videoInfo">
              <div class="videoName">购买《海贼王》</div>
              <div class="orderInfo">
                <p class="time">2020.08.5 11:34</p>
                <!-- <p class="payWay">微信支付</p> -->
              </div>
            </div>
            <div class="videoPrice">-300.00元</div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import { getOrderRecordData } from "../assets/javascript/apis.js";
export default {
  data() {
    return {
      pullLoading: false,
      disablePullRefresh: false,
      getDataFlag: true,
      pushLoading: false,
      finished: false,
      dataList: [],
      page: 1,
      pushError: false,
      successMessage: "",
    };
  },
  created() {
    console.log("orderRecord");
    this.getData();
  },
  methods: {
    //获取页面数据
    getData(page, refreshData = true) {
      //控制发起请求的频率
      if (!this.getDataFlag) return;
      this.getDataFlag = false;
      //发起请求前禁用下拉刷新
      this.disablePullRefresh = true;

      const data = {
        page: page ? page : 1,
      };
      getOrderRecordData(data)
        .then((res) => {
          console.log(res);
          //处理数据
          if (refreshData) {
            this.dataList = [];
          }
          this.dataList.push(res.data);
          this.page = res.meta.current_page;

          //可以重新发起请求
          this.getDataFlag = true;
          if (refreshData) {
            //展现下拉刷新成功提示信息
            this.successMessage = "刷新成功";
            // 加载数据成功，修改下拉刷新的状态
            this.pullLoading = false;
            //重新启用下拉刷新
            this.disablePullRefresh = false;
          } else {
            //加载数据成功后，修改上拉加载更多的状态
            this.pushLoading = false;
          }
          //当加载到最后一页的时候，禁用上拉加载更多功能
          if (res.meta.current_page >= res.meta.last_page) {
            this.finished = true;
          }
        })
        .catch((res) => {
          console.log(res);
          //可以重新发起请求
          this.getDataFlag = true;
          if (refreshData) {
            //展现下拉刷新成功提示信息
            this.successMessage = "刷新失败，请重新刷新";
            // 加载数据成功，修改下拉刷新的状态
            this.pullLoading = false;
            //重新启用下拉刷新
            this.disablePullRefresh = false;
          } else {
            //加载数据失败后，修改上拉加载更多的状态
            this.pushLoading = false;
            //当请求失败时，展现上拉加载更多失败信息
            this.pushError = true;
          }
        });
    },
    //下拉加载
    pullRefresh() {
      this.getData();
    },
    //上拉加载更多
    onLoad() {
      this.page = this.page + 1;
      this.getData(this.page, false);
    },
  },
};
</script>
<style lang="scss" scoped>
#orderRecord {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /deep/.van-pull-refresh {
    flex-grow: 1;
    overflow: visible;
    .van-pull-refresh__track {
      height: auto;
      .van-pull-refresh__head {
        background: blue;
      }
    }
  }
  .orderList {
    width: 100%;
    padding: 0 0 0 20px;
    box-sizing: border-box;
    .order {
      width: 100%;
      height: 65px;
      padding: 0 20px 0 0;
      box-sizing: border-box;
      background: white;
      border-bottom: 1px solid rgba(242, 244, 246, 1);
      display: flex;
      justify-content: space-between;
      align-items: center;
      .videoInfo {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        .videoName {
          font-size: 14px;
          color: rgba(7, 7, 7, 1);
        }
        .orderInfo {
          font-size: 12px;
          color: rgba(170, 170, 170, 1);
          display: flex;
          align-items: center;
          .payWay {
            margin-left: 20px;
          }
        }
      }
      .videoPrice {
        font-size: 18px;
        font-weight: 500;
        color: rgba(7, 7, 7, 1);
      }
    }
  }
}
</style>